.web-content-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
  > div {
    width: 40%;
    &:first-child,
    &:nth-child(2) {
      margin-bottom: 45px;
    }
    &:nth-child(2n) {
      width: 45%;
      margin-left: 12%;
    }
    > div {
      &:first-child {
        display: flex;
        align-items: center;
        img {
          width: 40px;
          margin-right: 20px;
        }
      }
      &:last-child {
        margin: 10px 0 0 60px;
      }
    }
  }
}
