.back-to-top {
  position: fixed;
  z-index: 999;
  bottom: 40px;
  right: 40px;

  .icon {
    width: 64px;
    height: 64px;
    background: url('../../assets/imgs/imgs/back_to_top.png') no-repeat center
      center;
    background-size: 100% 100%;
    transition: all 0.3s;
    opacity: 0;
    display: none;
    cursor: pointer;

    &.show {
      display: block;
      opacity: 1;
    }
  }
}
