.card-wrap {
  display: flex;
  justify-content: space-between;
  > div {
    width: 288px;
    height: 580px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    color: #3f3f3f;
    background: #fff;
    margin-top: 60px;
    > div,
    button {
      position: relative;
      z-index: 99;
    }
    &::before {
      content: '';
      position: absolute;
      width: 304px;
      height: 596px;
      left: -12px;
      top: -12px;
      border: 4px solid #ffffff;
      border-radius: 26px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      z-index: 33;
    }
    &:hover {
      &::before {
        opacity: 1;
      }
    }
    .row {
      width: 100%;
      height: 54px;
      line-height: 54px;
      text-align: center;
      border-top: 0.5px solid #c4c4c4;
    }
    button {
      width: 192px;
      height: 36px;
      border-radius: 50px;
      margin-top: 25px;
    }
    > div {
      text-align: center;
    }
  }
}
