.home-banner {
  position: relative;
  width: 1140px;
  height: calc(100vh - 85px);
  margin: auto;
  display: flex;
  align-items: center;
  &::before {
    content: '';
    position: absolute;
    width: 138px;
    height: 1px;
    left: 0;
    bottom: 135px;
    background: #000;
    opacity: 0.35;
  }
  div {
    position: relative;
    z-index: 99;
  }
  .bg {
    position: absolute;
    width: 650px;
    height: calc(100% - 15px);
    right: 0;
    bottom: 0;
    background: url('../../assets/imgs/imgs/home_banner.png') center / cover
      no-repeat;
    z-index: 33;
  }
  .text-1 {
    font-size: 84px;
    font-weight: bold;
    line-height: 100px;
    color: #0033cc;
  }
  .text-2 {
    font-size: 17px;
    line-height: 25px;
    color: #3f3f3f;
  }
  .text-3 {
    font-size: 20px;
    line-height: 28px;
    color: #6f6f6f;
  }
  .flex {
    display: flex;
  }
}
.banner {
  width: 100%;
  height: 580px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 25px;
  line-height: 35px;
  .title {
    font-size: 55px;
    text-align: center;
    font-weight: bold;
    line-height: 60px;
    margin-bottom: 20px;
  }
}
