.submit-request {
  width: 100%;
  padding: 70px 0;

  &:not(.home) {
    background: #f0f0f0;
  }

  &:not(.home) {
    .form-field {
      background: #fff !important;
    }
  }

  .container {
    width: 720px;
    margin: 0 auto;

    .title {
      font-style: normal;
      font-weight: 900;
      font-size: 35px;
      color: #3f3f3f;
      margin: 70px 0 0;
    }

    .form {
      .form-label {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #3f3f3f;
        &.required {
          &::before {
            content: '*';
            color: red;
            height: 17px;
            margin-right: 5px;
            display: inline-block;
          }
        }
      }

      .form-row {
        width: 720px;
        margin: 25px 0 0;

        .form-field {
          margin: 10px 0 0;
          width: 100%;
          height: 40px;
          background: #e5e5e5;

          &.textarea {
            height: 80px;

            .message {
              display: block;
              width: 100%;
              height: 100%;
              font-style: normal;
              font-weight: normal;
              font-size: 14px;
              line-height: 20px;
              color: #3f3f3f;
              text-indent: 1em;
              resize: none;
              background: transparent;
              padding: 1em;
              text-indent: 0;
              box-sizing: border-box;
            }
          }
        }
      }

      .form-row-half {
        display: flex;
        justify-content: space-between;

        .form-row {
          width: 342px;

          .form-input {
            display: block;
            width: 100%;
            height: 100%;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #3f3f3f;
            text-indent: 1em;
          }
        }
      }
    }

    .send-me-copy {
      width: 360px;
      margin: 30px auto;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: #3f3f3f;
      padding: 0 0 0 10px;
      display: flex;
      align-items: center;

      .checkbox {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: 0 10px 0 0;
        cursor: pointer;

        &:after {
          content: '';
          display: block;
          width: 20px;
          height: 20px;
          background: #e5e5e5;
        }

        &.checked {
          &:after {
            background: url(../../assets/imgs/icon/check.png) no-repeat center
              center;
            background-size: 100% 100%;
          }
        }
      }
    }

    .submit {
      display: block;
      width: 360px;
      height: 40px;
      border-radius: 20px;
      background: #0033cc;
      font-style: normal;
      font-weight: 900;
      font-size: 14px;
      color: #f5f9fc;
      text-align: center;
      line-height: 40px;
      margin: 0 auto;
      cursor: pointer;
    }
  }
}
