.description-area {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 100px 0 0 0;

  .description-item {
    background: #0033cc;
    padding: 24px 28px;
    color: #f0f0f0;
    margin: 0 0 10px;

    .title {
      font-style: normal;
      font-weight: 900;
      font-size: 20px;
      line-height: 30px;
      margin: 0 0 18px;
    }

    .description {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 25px;
    }
  }
}
