.footer {
  width: 100%;
  height: 180px;
  background: #202020;

  .container {
    width: 1140px;
    margin: 0 auto;

    .links-container {
      width: 100%;
      height: 92px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .logo {
        width: 172px;
        height: 26px;
        background: url(../../assets/imgs/imgs/logo_white.png) no-repeat center
          center;
        background-size: 100% 100%;
      }

      .links {
        .link-item {
          display: inline-block;
          margin: 0 20px;

          .link-element {
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            color: #ffffff;
          }
        }
      }
    }

    .copyright {
      width: 100%;
      height: 87px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      color: #e5e5e5;
      border-top: 1px solid rgba(255, 255, 255, 0.35);
    }
  }
}
