.home-content-1 {
  width: 100%;
  height: 250px;
  background: #f0f0f0;
  > div {
    width: 1140px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .btn-wrap {
      width: 405px;
      height: 45px;
      background: #fff;
      border-radius: 100px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px 0 24px;
      input {
        width: 270px;
      }
      button {
        width: 93px;
        height: 39px;
        border-radius: 100px;
      }
    }
  }
}
button {
  text-align: center;
  vertical-align: middle;
  font-size: 14px;
  font-weight: bold !important;
  color: #fff;
  background: #0033cc;
  cursor: pointer;
  &:hover {
    opacity: 0.8;
  }
}
.home-content-2 {
  display: flex;
  justify-content: space-between;
  > div {
    width: 260px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    > div,
    a {
      text-align: center;
    }
    .img-wrap {
      height: 55px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 18px;
      img {
        width: fit-content;
        height: fit-content;
      }
    }
    .grey-color {
      margin: 10px 0 25px;
    }
    a {
      width: fit-content;
      margin: auto;
    }
  }
}

.home-content-4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .btn-wrap {
    display: flex;
    align-items: center;
    button {
      width: 140px;
      height: 40px;
      border-radius: 50px;
      background: #f0f0f0;
      color: #0033cc;
    }
  }
}

.font-55 {
  font-size: 55px;
  line-height: 60px;
}
