.highlight-points {
  width: 450px;
  margin: 0 0 60px;

  .title {
    font-style: normal;
    font-weight: 900;
    font-size: 25px;
    line-height: 30px;
    color: #3f3f3f;
  }

  .points-container {
    margin: 35px 0 0;

    .point-row {
      margin: 0 0 15px;
      padding: 0 0 0 30px;
      background: url(../../assets/imgs/icon/cloud_tick.png) no-repeat left 4px;
      background-size: 16px 16px;
      font-style: normal;
      font-weight: normal;
      font-size: 17px;
      line-height: 25px;
      color: #6f6f6f;
    }
  }
}
