.title-component {
  margin: 0 auto;
}
.page-content-subtitle {
  width: 100%;
  font-size: 13px;
  line-height: 20px;
  margin-bottom: 20px;
  text-align: center;
}
.page-content-title {
  width: 100%;
  font-size: 45px;
  font-weight: bold;
  line-height: 50px;
  text-align: center;
}
.page-content {
  width: 100%;
  font-size: 16px;
  line-height: 25px;
  word-break: break-word;
  > div {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
