.menu {
  width: 100%;
  height: 85px;
  background: #ffffff;

  .container {
    // @media screen and (max-width: 768px) {
    //   width: 100%;
    //   padding: 0 18px;
    // }
    width: 1140px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo {
      width: 172px;
      height: 26px;
      background: url(../../assets/imgs/imgs/logo.png) no-repeat center center;
      background-size: 100% 100%;
    }

    .menu-links {
      display: flex;
      align-items: center;

      .links {
        .link-item {
          display: inline-block;
          margin: 0 20px;

          .link-element {
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            color: #000000;
            transition: all 0.3s;

            &:hover {
              color: #0033cc;
            }

            &.active {
              color: #0033cc;
            }
          }
        }
      }

      .contact-us {
        padding: 8px 36px;
        background: #0033cc;
        border-radius: 50px;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        text-align: center;
        color: #f5f9fc;
        margin-left: 55px;
      }
    }
  }
}
